<template>
	<div>
		<section class="imgBoxLunbo" v-show="imgshowObj.isimgshow">
			<div class="swiper-container swiper-container-horizontal swiper-container-ios">
				<div :class="{GGup:imgNum>0}" @click="imgreduce"></div>
				<div :class="{GGnext:(imgNum+1)<imgArrLength}" @click="imgPlus"></div>
				<div class="swiper-wrapper" :style="imgNumss">
					<div class="swiper-slide " v-for="item in imgshowObj.imgshowArr"><img class="swiper-lazy swiper-lazy-loaded" :src="item"></div>

				</div>

				<div class="swiper-pagination swiper-pagination-fraction">
					<span class="swiper-pagination-current">{{imgNum+1}}</span> / <span class="swiper-pagination-total">{{imgArrLength}}</span></div>
				<span class="swiper-notification" aria-live="assertive" aria-atomic="true"></span></div>
			<div class="close_btn">
				<a class="close" @click="closeImgshow">X</a>
			</div>
		</section>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isimgshow: false,
				imgNum:0,
				imgArrLength:0

			}
		},
		computed: { //获取仓库信息，用函数接收一个对象。可以当做data里面的数据直接使用
			imgshowObj() {
				return this.$store.getters.getimgshowObj;
			},
			imgNumss(){
				return {
					transform: `translate3d(-${this.imgNum*100}%, 0px, 0px)`};
			}
		},
		methods: {
			closeImgshow() {
				this.$store.dispatch("setimgshowObj", {
					isimgshow:false,
					imgshowArr:[],
					imgshowIndex:0
					
				})
			},
			imgPlus(){
				this.imgNum +=1;
			},
			imgreduce(){
				this.imgNum -=1;
			}
		},
		watch:{//配合仓库进行首次页面加载工作
			imgshowObj:function(a){
				if(a!=undefined){
					this.imgNum = a.imgshowIndex;
					this.imgArrLength = a.imgshowArr.length
				}
			}
		}

	};
</script>

